.anime-card {
	position: relative;
	display: flex;
	cursor: pointer;
	flex-direction: column;
	align-items: center;
	padding: 1rem;
}
.anime-card::before {
	content: "";
	position: absolute;
	content: "";
	top: 0px;
	right: 0px;
	bottom: 0px;
	left: 0px;
	content: "";
	z-index: -1;
	content: "";
	display: block;
	content: "";
	height: 100%;
	content: "";
	width: 100%;
	content: "";
	--tw-scale-x: 1;
	--tw-scale-y: 1;
	transform: var(--tw-transform);
	content: "";
	border-radius: 0.75rem;
	content: "";
	--tw-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
	box-shadow: var(--tw-ring-offset-shadow, 0 0 #0000), var(--tw-ring-shadow, 0 0 #0000), var(--tw-shadow);
	content: "";
	transition-property: all;
	transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
	transition-duration: 150ms;
}
.anime-card:hover::before {
	content: "";
	--tw-scale-x: 1.02;
	--tw-scale-y: 1.02;
	transform: var(--tw-transform);
}
.img-wrap {
	position: relative;
	margin-bottom: 1.5rem;
	height: 16rem;
	width: 100%;
}
.img-full {
	height: 100%;
	width: 100%;
	object-fit: cover;
	object-position: top;
}
.pagination {
	margin: auto;
	margin-top: 2rem;
	display: flex;
	max-width: 80rem;
}
.pagination-item {
	position: relative;
	display: flex;
}
.pagination-link, .pagination-prev, .pagination-next {
	display: flex;
	flex: 1 1 0%;
	border-radius: 0.375rem;
	--tw-bg-opacity: 1;
	background-color: rgba(229, 231, 235, var(--tw-bg-opacity));
	padding-left: 1rem;
	padding-right: 1rem;
	padding-top: 0.5rem;
	padding-bottom: 0.5rem;
	--tw-text-opacity: 1;
	color: rgba(55, 65, 81, var(--tw-text-opacity));
}
.pagination-link:hover, .pagination-prev:hover, .pagination-next:hover {
	--tw-bg-opacity: 1;
	background-color: rgba(96, 165, 250, var(--tw-bg-opacity));
	--tw-text-opacity: 1;
	color: rgba(255, 255, 255, var(--tw-text-opacity));
}
.pagination-prev, .pagination-next {
	--tw-bg-opacity: 1;
	background-color: rgba(209, 213, 219, var(--tw-bg-opacity));
	font-weight: 700;
	--tw-text-opacity: 1;
	color: rgba(107, 114, 128, var(--tw-text-opacity));
}
.pagination-link-active {
	--tw-bg-opacity: 1;
	background-color: rgba(96, 165, 250, var(--tw-bg-opacity));
	--tw-text-opacity: 1;
	color: rgba(255, 255, 255, var(--tw-text-opacity));
}
.pagination-disabled {
	cursor: not-allowed;
	--tw-bg-opacity: 1;
	background-color: rgba(229, 231, 235, var(--tw-bg-opacity));
	--tw-text-opacity: 1;
	color: rgba(107, 114, 128, var(--tw-text-opacity));
	--tw-grayscale: grayscale(0);
	filter: var(--tw-filter);
}
.pagination-disabled:hover {
	-webkit-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
	--tw-bg-opacity: 1;
	background-color: rgba(229, 231, 235, var(--tw-bg-opacity));
	--tw-text-opacity: 1;
	color: rgba(107, 114, 128, var(--tw-text-opacity));
}
.m-auto {
	margin: auto;
}
.mb-4 {
	margin-bottom: 1rem;
}
.grid {
	display: grid;
}
.max-w-7xl {
	max-width: 80rem;
}
.grid-cols-5 {
	grid-template-columns: repeat(5, minmax(0, 1fr));
}
.gap-4 {
	gap: 1rem;
}
.text-center {
	text-align: center;
}
.text-lg {
	font-size: 1.125rem;
	line-height: 1.75rem;
}
.font-semibold {
	font-weight: 600;
}
.text-green-500 {
	--tw-text-opacity: 1;
	color: rgba(16, 185, 129, var(--tw-text-opacity));
}
.line-clamp-3 {
	overflow: hidden;
	display: -webkit-box;
	-webkit-box-orient: vertical;
